@onm-cal-font-color: #303030;
@onm-cal-font-color-soft: lighten(@onm-cal-font-color, 60%);
@onm-cal-font-color-highlight: #AE442F;

@onm-cal-font-color-inactive: lighten(@onm-cal-font-color, 40%);
@onm-cal-font-color-selected: #FFF;
@onm-cal-font-color-active: @onm-cal-font-color;
@onm-cal-font-bg-selected: #818181;
@onm-cal-font-bg-active: lighten(@onm-cal-font-bg-selected, 40%);

.onm-cal {
    &, *, *:before, *:after {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }
}

.onm-cal-popup {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    position: absolute;
    z-index: 200000;
}

.onm-cal {
    width: 100%;
    min-height: 20px;
    padding: 0;
    border: 1px solid #e3e3e3;
    border-radius: 1px;
    background-color: #FFF;
    min-width: 200px;
    max-width: 600px;
    
    .onm-cal-nav {
        position: relative;
        margin: 2px 0 0 0;
        height: 48px;
        
        button {
            position: absolute;
            top: 0;
            width: 48px;
            height: 48px;
            border: 0;
            cursor: pointer;
            margin: 0;
            padding: 0;
            background-color: transparent;
            background-repeat: no-repeat;
            background-position: center;
            background-size: 6px auto;
            
            &.onm-cal-prev {
                left: 18px;
                background-image: url('data:image/png;base64,');
            }
            &.onm-cal-next {
                right: 18px;
                background-image: url('data:image/png;base64,');
            }
            
            &:disabled {
                opacity: 0.33;
                cursor: default;
            }
        }
        
        h6 {
            margin: 0 66px;
            text-align: center;
            font-size: 16px;
            font-style: normal;
            font-weight: 500;
            line-height: 48px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            
            .onm-cal-month {
                padding: 0 4px;
                color: @onm-cal-font-color;
            }
            .onm-cal-year {
                padding: 0 4px;
                color: @onm-cal-font-color-soft;
            }
        }
    }
    .onm-cal-headline {
        height: 20px;
        font-size: 14px;
        line-height: 18px;
        font-style: normal;
        font-weight: 300;
        color: @onm-cal-font-color;
        margin: 0 18px 0 18px;
        
        > div {
            float: left;
            width: 14.035%;
            width: e("calc(100% / 7 - 1px)");
            margin-left: 0.292%;
            margin-left: e("calc(1px)");
            padding: 0 0 0 4px;
            
            &.onm-cal-line-start {
                margin-left: 0;
            }
            &.onm-cal-day0 {
                color: @onm-cal-font-color-highlight;
            }
        }
    }
    .onm-cal-content {
        clear: both;
        height: auto;
        margin: 0 18px;
        font-size: 14px;
        line-height: 18px;
        font-style: normal;
        font-weight: 500;
        color: @onm-cal-font-color-inactive;
        
        > div {
            position: relative;
            float: left;
            width: 14.035%;
            width: e("calc(100% / 7 - 1px)");
            margin-left: 0.292%;
            margin-left: e("calc(1px)");
            margin-top: 0.292%;
            margin-top: e("calc(1px)");
            padding-top: 14.035%;
            padding-top: e("calc(100% / 7 - 1px)");
            height: 0;
            
            &.onm-cal-line-start {
                margin-left: 0;
            }
            
            > div {
                position: absolute;
                left: 0;
                bottom: 0;
                right: 0;
                height: 24px;
                padding: 0 8px 0 0;
                text-align: right;
            }
        }
            
        .onm-cal-day-active {
            color: @onm-cal-font-color-active;
            background-color: @onm-cal-font-bg-active;
            cursor: pointer;
        }
        
        .onm-cal-day-selected {
            color: @onm-cal-font-color-selected;
            background-color: @onm-cal-font-bg-selected;
        }
        .onm-cal-day-selected-line:before {
            content: " ";
            display: block;
            position: absolute;
            width: 100%;
            top: 10px;
            height: 2px;
            background-color: #FFF;
        }
        .onm-cal-day-selected-start {
            &:before {
                content: " ";
                display: block;
                position: absolute;
                width: auto;
                left: 10px;
                right: 0;
                top: 10px;
                height: 2px;
                background-color: #FFF;
            }
            &:after {
                content: " ";
                display: block;
                position: absolute;
                width: 0;
                height: 0;
                top: 6px;
                left: 5px;
                border: 5px solid #FFF;
                border-radius: 5px;
            }
            
            &.onm-cal-day-selected-end {
                &:before,
                &:after {
                    display: none;
                }
            }
        }
        .onm-cal-day-selected-end {
            &:before {
                content: " ";
                display: block;
                position: absolute;
                width: auto;
                left: 0;
                right: 10px;
                top: 10px;
                height: 2px;
                background-color: #FFF;
            }
            &:after {
                content: " ";
                display: block;
                position: absolute;
                width: 0;
                height: 0;
                top: 6px;
                right: 5px;
                border: 5px solid #FFF;
                border-radius: 5px;
            }
        }
        
        &:after {
            content: " ";
            display: block;
            clear: both;
            height: 18px;
        }
    }
    
    &.onm-cal-small {
        .onm-cal-content {
            > div {
                > div {
                    position: absolute;
                    left: 0;
                    top: 50%;
                    right: 0;
                    margin-top: -11px;
                    height: 24px;
                    line-height: 24px;
                    padding: 0;
                    text-align: center;
                }
                &.onm-cal-day-selected-line:before {
                    top: 3px;
                }
                &.onm-cal-day-selected-start {
                    &:before {
                        top: 3px;
                    }
                    &:after {
                        top: 1px;
                        border: 3px solid #FFF;
                        border-radius: 3px;
                    }
                }
                &.onm-cal-day-selected-end {
                    &:before {
                        top: 3px;
                    }
                    &:after {
                        top: 1px;
                        border: 3px solid #FFF;
                        border-radius: 3px;
                    }
                }
            }
        }
    }
    
    &.onm-cal-no-nav {
        button {
            display: none;
        }
        
        h6 {
            margin: 0 18px;
            text-align: left;
        }
    }
}

.onm-cal-xcol {
    .onm-cal {
        float: left;
    }
    &:after {
        content: " ";
        display: block;
        clear: both;
    }
}
.onm-cal-2col {
    .onm-cal-xcol();
    .onm-cal {
        width: 50%;
    }
}
.onm-cal-3col {
    .onm-cal-xcol();
    .onm-cal {
        width: 33.33%;
    }
}
.onm-cal-4col {
    .onm-cal-xcol();
    .onm-cal {
        width: 25%;
    }
}